import axios from 'axios';
import store from '@/store'; // Importa tu store Vuex

const instance = axios.create({
  baseURL: store.getters.get__url, // Usa la URL de tu store Vuex
  timeout: 10000, // Tiempo máximo de espera para las solicitudes (en milisegundos)
});

// Interceptor para agregar el token de autorización a cada solicitud
instance.interceptors.request.use(
  config => {
    const token = store.getters.get__token; // Obtiene el token del store Vuex
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// Interceptor para reintentar la solicitud en caso de fallo
instance.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    let retries = 0;
    const maxRetries = 3;

    const retryRequest = () => {
      retries++;
      return instance(error.config);
    };

    if (error.response && error.response.status >= 500 && retries < maxRetries) {
      return retryRequest();
    }

    return Promise.reject(error);
  }
);

export default instance;
